/**axios
 * 
 */
import Vue from 'vue';
import router from '../router/index';
import http from './ajax';
import VueCookies from 'vue-cookies';
import axios from 'axios';
import { Message, Loading } from 'element-ui';
import _ from 'lodash';
let v = new Vue();

// import store from '../store/index'
// 
// let rootsd = process.env.NODE_ENV;
// switch (rootsd) {
//     case "development": //
//         axios.defaults.baseURL = 'http://xyj.grapefruitcloud.com';
//         break;
//     case "production": //
//         axios.defaults.baseURL = 'http://xyj.grapefruitcloud.com';
//         break;
//     default: //
//         axios.defaults.baseURL = 'http://zzb.onezen.net';
// }
var href = window.location.href;

if (href.indexOf('xyj.grapefruitcloud.com') >= 0) {
    var userUrl = 'http://xyj.grapefruitcloud.com'; //PHPURL
} else {
    var userUrl = 'http://zzb.onezen.net';
}
// 
//loading
var loading;

//
let needLoadingRequestCount = 0;
//loading
function showLoading(target) {
    // loading
    // needLoadingRequestCount0.loading
    if (needLoadingRequestCount === 0 && !loading) {
        loading = Loading.service({
            lock: true,
            text: 'Loading...',
            background: 'rgba(255, 255, 255, 0.5)',
            target: target || 'body'
        });
    }
    needLoadingRequestCount++;
}

//loading
function hideLoading() {
    needLoadingRequestCount--;
    needLoadingRequestCount = Math.max(needLoadingRequestCount, 0); //
    if (needLoadingRequestCount === 0) {
        //loading
        toHideLoading();
    }
}
// 300ms  loading  loading
var toHideLoading = _.debounce(() => {
    /**
     * Loadingclose()Loading
     */
    if (loading) {
        loading.close();
        loading = null;
    }
}, 300);
// 
axios.defaults.timeout = 10000;
// post
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';

// 
axios.interceptors.request.use(
    (config) => {

        var checkwithout = [
            userUrl + '/clouduser/loginbyphone',
            userUrl + '/clouduser/login',
            userUrl + '/clouduser/register',
            userUrl + '/clouduser/getcode',
            userUrl + '/clouduser/getemail',
            userUrl + '/clouduser/checktoken',
            userUrl + '/clouduser/forgetpassword',
            userUrl + '/clouduser/resetpassword',
        ];
        if (checkwithout.indexOf(config.url) < 0) {
            if (!VueCookies.get('id')) {
                alert('');
                alert_is_present();
                router.push('/');
                return false;
            }
            http.request({
                url: userUrl + '/clouduser/checktoken',
                method: 'post',
                data: {
                    id: VueCookies.get('id'),
                    token: VueCookies.get('token'),
                },
                async: true,
                success: function(data) {
                    data = JSON.parse(data);
                    if (data.status != 0) {
                        VueCookies.set('id', '', 0);
                        VueCookies.set('token', '', 0);
                        VueCookies.set('user', '', 0);
                        v.$alert(data.msg, '', {
                            confirmButtonText: '',
                            showClose: false,
                            center: true,
                            callback: (action) => {
                                router.push('/');
                            },
                        });
                    }
                },
            });
        }

        if (config.headers.showLoading !== false) {
            showLoading(config.headers.loadingTarget);
        }
        return config;
        // tokenhttpheadertoken
        // tokentoken
        // const token = store.state.token;
        // token && (config.headers.Authorization = token);
    },
    (error) => {
        //Loading
        if (config.headers.showLoading !== false) {
            hideLoading();
        }
        Message.error('!');
        return Promise.error(error);
    }
);
// 
axios.interceptors.response.use(
    (response) => {

        if (response.config.headers.showLoading !== false) {
            hideLoading();
        }
        //console.log(response.status)
        if (response.status === 200) {
            //console.log(Promise.resolve(response));
            if (response.data.status == -5) {
                Message.error('!');
            }
            // if (response.data.status == -900) {
            //     alert(response.data.msg);
            //     router.push('/');
            //     return false;
            // }
            return Promise.resolve(response);
        } else {
            return Promise.reject(response);
        }
    },
    // 200
    (error) => {
        //Loading
        if (error.config.headers.showLoading !== false) {
            hideLoading();
        }
        if (error.response && error.response.status == 404) {
            router.push('/');
        }
        //console.log(error.response.status);
        if (error.response.status) {
            switch (error.response.status) {
                // 401: 
                // 
                // 
                case 400:
                    Message.error('!');
                    break;
                    // 403 token
                    // 
                    // tokenvuextoken
                    // 
                case 404:
                    Message.error('!');
                    break;
                    // 404
                case 500:
                    Message.error('!');
                    break;
                    // 
                default:
                    this.$message(error.response.data.message);
            }
            return Promise.reject(error.response);
        }
    }
);
/**
 * getget
 * @param {String} url [url]
 * @param {Object} params []
 */
export function get(url, params) {
    return new Promise((resolve, reject) => {
        axios
            .get(url, {
                params: params,
            })
            .then((res) => {
                resolve(res.data);
            })
            .catch((err) => {
                // console.log(err.data);
                reject(err.data);
            });
    });
}
/**
 * postpost
 * @param {String} url [url]
 * @param {Object} params []
 */
export function post(url, params) {
    return new Promise((resolve, reject) => {
        axios
            .post(url, params)
            .then((res) => {
                resolve(res.data);
            })
            .catch((err) => {
                reject(err.data);
            });
    });
}


